import React, { useEffect } from 'react';
import { Canvas } from './Canvas';

export const Node: React.FC = () => {
    useEffect(() => {
        const ctx: any = document?.getElementById('myCanvas')?.getContext('2d');
        ctx.beginPath();
        ctx.arc(75, 75, 50, 0, Math.PI * 2, true);
        ctx.lineWidth = 2;
        ctx.stroke();
        ctx.font = '30px Arial';
        ctx.fillText('5', 65, 85);
    }, []);

    return (
        <>
            <Canvas></Canvas>
        </>
    );
};
